<h2 id="网络配置" tabindex="-1"><a class="header-anchor" href="#网络配置" aria-hidden="true">#</a> 网络配置</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token builtin class-name">cd</span> /etc/sysconfig/network-scripts
查看ifcfg-ens开头的配置文件并修改

<span class="token assign-left variable">TYPE</span><span class="token operator">=</span>Ethernet
<span class="token assign-left variable">PROXY_METHOD</span><span class="token operator">=</span>none
<span class="token assign-left variable">BROWSER_ONLY</span><span class="token operator">=</span>no
<span class="token comment"># ip获取方式 dhcp static</span>
<span class="token assign-left variable">BOOTPROTO</span><span class="token operator">=</span>dhcp
<span class="token assign-left variable">DEFROUTE</span><span class="token operator">=</span>yes
<span class="token assign-left variable">IPV4_FAILURE_FATAL</span><span class="token operator">=</span>no
<span class="token assign-left variable">IPV6INIT</span><span class="token operator">=</span>yes
<span class="token assign-left variable">IPV6_AUTOCONF</span><span class="token operator">=</span>yes
<span class="token assign-left variable">IPV6_DEFROUTE</span><span class="token operator">=</span>yes
<span class="token assign-left variable">IPV6_FAILURE_FATAL</span><span class="token operator">=</span>no
<span class="token assign-left variable">IPV6_ADDR_GEN_MODE</span><span class="token operator">=</span>stable-privacy
<span class="token assign-left variable">NAME</span><span class="token operator">=</span>ens33
<span class="token assign-left variable">UUID</span><span class="token operator">=</span>f26ae8b9-79d5-43f1-87f5-867827419eab
<span class="token assign-left variable">DEVICE</span><span class="token operator">=</span>ens33
<span class="token comment"># 启用</span>
<span class="token assign-left variable">ONBOOT</span><span class="token operator">=</span>yes

<span class="token assign-left variable">IPADDR</span><span class="token operator">=</span><span class="token number">192.168</span>.0.107
<span class="token assign-left variable">PREFIX</span><span class="token operator">=</span><span class="token number">24</span>
<span class="token assign-left variable">GATEWAY</span><span class="token operator">=</span><span class="token number">192.168</span>.0.1
<span class="token assign-left variable">DNS1</span><span class="token operator">=</span><span class="token number">114.114</span>.114.114
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br></div></div><p>保存后重启网络服务：systemctl restart network</p>
<h2 id="常用命令" tabindex="-1"><a class="header-anchor" href="#常用命令" aria-hidden="true">#</a> 常用命令</h2>
<h3 id="ls-列出目录" tabindex="-1"><a class="header-anchor" href="#ls-列出目录" aria-hidden="true">#</a> ls 列出目录</h3>
<p>ls -lrt 表示 按修改时间 升序 列出当前工作目录下的所有文件的详细信息</p>
<p>1）-l 表示开启长列表输出，打开了就会输出文件权限、引用计数、所有者、所属组、文件大小、修改日期和文件名称这些详细的信息。</p>
<p>2）-t 以时间排序，最新的文件会排在上面。</p>
<p>3）-r 表示反向排序、倒序输出。</p>
<p>4）-x 按列输出，横向排序。</p>
<p>5）-u 按照文件上次被访问的时间排序。</p>
<p>统计某文件夹下文件的个数
ls -l |grep &quot;^-&quot;|wc -l</p>
<p>查找以ab开头py结尾的文件
ls | grep '^ab.*py$'</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>
ll
<span class="token function">dir</span>

<span class="token comment"># 列出目录树</span>
<span class="token comment"># tree [dir]</span>

<span class="token comment"># 编辑文件</span>
<span class="token comment"># vi 文件</span>
<span class="token function">vi</span> 

<span class="token comment"># 编辑文件</span>
<span class="token comment"># vim 文件</span>
<span class="token function">vim</span> 

<span class="token comment"># 安装</span>
<span class="token comment"># yum install -y 软件名</span>
yum <span class="token function">install</span> -y 

<span class="token comment"># 更改文件所有者</span>
<span class="token function">chown</span> 用户名 文件
<span class="token comment"># 更改文件群组</span>
<span class="token function">chgrp</span> -R 群组 文件

</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br></div></div><h2 id="查询命令" tabindex="-1"><a class="header-anchor" href="#查询命令" aria-hidden="true">#</a> 查询命令</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token comment"># 查看历史记录</span>
