<p>/etc/init.d/bt default</p>
<p>##一、iptables防火墙</p>
<h3 id="_1、基本操作" tabindex="-1"><a class="header-anchor" href="#_1、基本操作" aria-hidden="true">#</a> 1、基本操作</h3>
<p>查看防火墙状态</p>
<p>service iptables status</p>
<h3 id="停止防火墙" tabindex="-1"><a class="header-anchor" href="#停止防火墙" aria-hidden="true">#</a> 停止防火墙</h3>
<p>service iptables stop</p>
<h3 id="启动防火墙" tabindex="-1"><a class="header-anchor" href="#启动防火墙" aria-hidden="true">#</a> 启动防火墙</h3>
<p>service iptables start</p>
<h3 id="重启防火墙" tabindex="-1"><a class="header-anchor" href="#重启防火墙" aria-hidden="true">#</a> 重启防火墙</h3>
<p>service iptables restart</p>
<h3 id="永久关闭防火墙" tabindex="-1"><a class="header-anchor" href="#永久关闭防火墙" aria-hidden="true">#</a> 永久关闭防火墙</h3>
<p>chkconfig iptables off</p>
<h3 id="永久关闭后重启" tabindex="-1"><a class="header-anchor" href="#永久关闭后重启" aria-hidden="true">#</a> 永久关闭后重启</h3>
<p>chkconfig iptables on</p>
<p>2、开启80端口</p>
<p>vim /etc/sysconfig/iptables</p>
<h3 id="加入如下代码" tabindex="-1"><a class="header-anchor" href="#加入如下代码" aria-hidden="true">#</a> 加入如下代码</h3>
<p>-A INPUT -m state --state NEW -m tcp -p tcp --dport 80 -j ACCEPT
保存退出后重启防火墙</p>
<p>service iptables restart
二、firewall防火墙
1、查看firewall服务状态</p>
<p>systemctl status firewalld</p>
<p>出现Active: active (running)切高亮显示则表示是启动状态。</p>
<p>出现 Active: inactive (dead)灰色表示停止，看单词也行。
2、查看firewall的状态</p>
<p>firewall-cmd --state
3、开启、重启、关闭、firewalld.service服务</p>
<h3 id="开启" tabindex="-1"><a class="header-anchor" href="#开启" aria-hidden="true">#</a> 开启</h3>
<p>service firewalld start</p>
<h3 id="重启" tabindex="-1"><a class="header-anchor" href="#重启" aria-hidden="true">#</a> 重启</h3>
<p>service firewalld restart</p>
<h3 id="关闭" tabindex="-1"><a class="header-anchor" href="#关闭" aria-hidden="true">#</a> 关闭</h3>
<p>service firewalld stop
4、查看防火墙规则</p>
<p>firewall-cmd --list-all
5、查询、开放、关闭端口</p>
<h3 id="查询端口是否开放" tabindex="-1"><a class="header-anchor" href="#查询端口是否开放" aria-hidden="true">#</a> 查询端口是否开放</h3>
<p>firewall-cmd --query-port=8080/tcp</p>
<h3 id="开放80端口" tabindex="-1"><a class="header-anchor" href="#开放80端口" aria-hidden="true">#</a> 开放80端口</h3>
<p>firewall-cmd --permanent --add-port=80/tcp</p>
<h3 id="移除端口" tabindex="-1"><a class="header-anchor" href="#移除端口" aria-hidden="true">#</a> 移除端口</h3>
<p>firewall-cmd --permanent --remove-port=8080/tcp
###重启防火墙(修改配置后要重启防火墙)
firewall-cmd --reload</p>
<h3 id="参数解释" tabindex="-1"><a class="header-anchor" href="#参数解释" aria-hidden="true">#</a> 参数解释</h3>
<p>1、firwall-cmd：是Linux提供的操作firewall的一个工具；
2、--permanent：表示设置为持久；
3、--add-port：标识添加的端口；</p>
<h2 id="关闭selinux" tabindex="-1"><a class="header-anchor" href="#关闭selinux" aria-hidden="true">#</a> 关闭SElinux</h2>
<p>1、查看selinux状态</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>getenforce   <span class="token comment">#Enforcing         表示启动</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>2、临时关闭</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token punctuation">[</span>root@localhost ~<span class="token punctuation">]</span><span class="token comment"># setenforce </span>
             usage:  setenforce <span class="token punctuation">[</span> Enforcing <span class="token operator">|</span> Permissive <span class="token operator">|</span> <span class="token number">1</span> <span class="token operator">|</span> <span class="token number">0</span> <span class="token punctuation">]</span>     <span class="token number">1</span>表示启动，0表示关闭
             <span class="token punctuation">[</span>root@localhost ~<span class="token punctuation">]</span><span class="token comment"># setenforce 0                          临时关闭</span>
             <span class="token punctuation">[</span>root@localhost ~<span class="token punctuation">]</span><span class="token comment"># getenforce                            查看状态</span>
             Permissive                                                关闭状态
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><p>3、永久关闭（修改配置文件，即可永久关闭）</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token punctuation">[</span>root@localhost ~<span class="token punctuation">]</span><span class="token comment"># vi /etc/selinux/config </span>
             <span class="token comment"># This file controls the state of SELinux on the system.</span>
             <span class="token comment"># SELINUX= can take one of these three values:</span>
             <span class="token comment">#     enforcing - SELinux security policy is enforced.</span>
             <span class="token comment">#     permissive - SELinux prints warnings instead of enforcing.</span>
             <span class="token comment">#     disabled - No SELinux policy is loaded.</span>
             <span class="token assign-left variable">SELINUX</span><span class="token operator">=</span>enforcing         修改为<span class="token string">"SELINUX=disabled"</span>
             <span class="token comment"># SELINUXTYPE= can take one of three values:</span>
             <span class="token comment">#     targeted - Targeted processes are protected,</span>
             <span class="token comment">#     minimum - Modification of targeted policy. Only selected processes are protected.</span>
             <span class="token comment">#     mls - Multi Level Security protection.</span>
             <span class="token assign-left variable">SELINUXTYPE</span><span class="token operator">=</span>targeted
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br></div></div>